@import url(//fonts.googleapis.com/css?family=Actor);
@import url(//fonts.googleapis.com/css?family=Bebas Neue);
.header{background-color:#0a0a0a;display:flex;flex-direction:row;justify-content:space-between;text-align:left}@media all and (min-width: 1000px){.header{flex-direction:row}}.header__title{color:#e50914;padding:20px;font-family:'Bebas Neue';font-size:60px;-webkit-animation:pulse 1s infinite ease-in-out alternate;animation:pulse 1s infinite ease-in-out alternate}@-webkit-keyframes pulse{from{-webkit-transform:scale(0.8);transform:scale(0.8)}to{-webkit-transform:scale(1.2);transform:scale(1.2)}}@keyframes pulse{from{-webkit-transform:scale(0.8);transform:scale(0.8)}to{-webkit-transform:scale(1.2);transform:scale(1.2)}}.header__menu{justify-content:space-around}@media all and (min-width: 768px){.header__menu{display:flex}}.header_nav{display:flex;align-items:center;margin-top:10px}.header__navItem{list-style:none;padding:5px 20px 5px 5px;text-align:end}.header__navLink{color:#e50914;list-style:none;cursor:pointer;padding-top:10px;font-family:'Bebas Neue';font-size:20px}@media all and (min-width: 768px){.header__navLink{padding:20px}}@media all and (min-width: 1000px){.header__navLink{padding:30px;font-size:30px}}.header__navLink:hover{color:#bfb8b9}

.cards{display:flex;flex-direction:row;flex-wrap:wrap;list-style:none;justify-content:space-around;margin:0px 50px 0px 50px}.card{display:flex;flex-direction:row;flex-wrap:wrap;list-style:none;justify-content:space-between;margin-right:0.5rem;margin-bottom:0.5rem;max-width:7.5rem;border-radius:0.5rem;background-color:white;border:0.1875rem solid #e50914;overflow:hidden;transition:0.25s all;background-color:#0a0a0a;color:#cecece}.card__img{width:100%}.card__title{margin:10px auto}.card__description{margin:0 auto}

.movies__filters{display:flex;align-items:center}.movies__filters>*{width:50%}.main__section1{color:#cecece;background-color:#0a0a0a;display:flex;flex-direction:column;text-align:center}.main__section1-title{font-size:30px;font-weight:700;padding:10px;font-family:'Bebas Neue'}.movies__filters{display:flex;align-items:center;flex-direction:column;padding:20px}@media all and (min-width: 1000px){.movies__filters{flex-direction:row}}.movies__filters--gender{margin-top:10px}.form__input-text{margin:10px}.form__input-text{color:#0a0a0a;background-color:#cecece;margin-left:5px}.movies__radio{display:inline-block;margin:0 0.5rem}

.main__section2{display:flex;flex-direction:column;justify-content:space-between;text-align:center}.main__section2--title{font-family:'Bebas Neue';font-size:40px}.main__section2--form{display:flex;flex-direction:column;justify-content:space-between;text-align:center;width:30%;margin:0 auto}.form__label,.form__btn{margin:20px}.form__btn{color:#0a0a0a}

.main__section3{display:flex;flex-direction:column;justify-content:space-between;text-align:center}.main__section3--title{font-family:'Bebas Neue';font-size:40px}.main__section3--form{display:flex;flex-direction:column;justify-content:space-between;text-align:center;width:30%;margin:0 auto}.form__label,.form__btn{margin:20px}.form__btn{color:#0a0a0a}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Actor', sans-serif;text-decoration:none;color:#bfb8b9}body{background-color:#0a0a0a}

